export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			username:'',
			password:'',
			userInfo:{}
		}
	},
	methods: {
		iconBack() {
			uni.switchTab({
				url:'../my/my'
			})
		},
		getUsername(e){
			this.username=e.detail.value
		},
		getPassword(e){
			this.password=e.detail.value
		},
		login(){
			let userBOs={username:this.username,password:this.password}
			uni.request({
				url: `https://www.imovietrailer.com/superhero/user/registOrLogin?qq=2684425594`,
				method: 'POST',
				data: {
					password:this.password,
					username:this.username
				},
				success: res => {
					console.log(res)
					if(res.data.status===200){
						uni.showToast({
							title:'登录成功',
							icon:'success'
						})
						let userInfo=res.data.data
						let usernames =res.data.data.username
						uni.setStorageSync('username',usernames)
						uni.setStorageSync('userinfo',userInfo)
						this.$store.state.username=usernames
						this.$store.state.userInfo=userInfo
						console.log(this.$store.state.username)
						uni.switchTab({
							url:'../my/my'
						})
						uni.hideToast()
					}
					if(res.data.status===500){
						uni.showToast({
							title:'用户名或密码错误',
							icon:'none'
						})
						uni.hideToast()
					}
				},
				fail: (err) => {
					console.log(err)
				},
				complete: () => {}
			});
		}
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
